import { box, component, Ref, onMouseDown, Events, invalidate, useEffect, findDOMNode, _, } from "ivi";
import { div } from "ivi-html";
import * as styles from "./slider.scss";
function clamp(x, min, max) {
    return x < min ? min : x > max ? max : x;
}
function noOp() { }
export const Slider = component(c => {
    let containerRef = box(null);
    let isDown = false;
    let handler = noOp;
    function signal(ev) {
        const bounds = findDOMNode(containerRef).getBoundingClientRect();
        const Rem = 16;
        const dotWidth = Rem * 0.75;
        const width = bounds.width - dotWidth;
        const localX = clamp(ev.clientX - bounds.left - dotWidth / 2, 0, width);
        handler(localX / width);
    }
    function onDown(ev) {
        signal(ev);
        isDown = true;
        invalidate(c);
    }
    function onUp() {
        isDown = false;
        invalidate(c);
    }
    function onMove(ev) {
        if (isDown) {
            signal(ev);
        }
    }
    const listenToMouse = useEffect(c, () => {
        document.body.addEventListener("mouseup", onUp, { passive: true });
        document.body.addEventListener("mousemove", onMove, {
            passive: true,
        });
        return () => {
            document.body.removeEventListener("mouseup", onUp);
            document.body.removeEventListener("mousemove", onMove);
        };
    });
    return props => {
        handler = props.onChange;
        listenToMouse();
        const color = props.color !== undefined ? props.color.toStyle() : undefined;
        const percentage = Math.max(0, Math.min(1, props.percentage));
        //
        return Events(onMouseDown(onDown), Ref(containerRef, div(styles.container, _, [
            div(_, _, [
                div(percentage === 0 ? styles.emptyButton : styles.button, {
                    style: {
                        left: "calc(" + percentage + " * calc(100% - 0.75rem))",
                        "background-color": color,
                    },
                }),
                div(styles.filledLineClass, {
                    style: {
                        width: percentage * 100 + "%",
                        "background-color": color,
                    },
                }),
                div(styles.baseLine),
            ]),
        ])));
    };
});
