import { Vec2 } from "../util";
export var MsgType;
(function (MsgType) {
    MsgType[MsgType["SetZoomMsg"] = 0] = "SetZoomMsg";
    MsgType[MsgType["SetOffsetMsg"] = 1] = "SetOffsetMsg";
    MsgType[MsgType["SetRotationMsg"] = 2] = "SetRotationMsg";
})(MsgType || (MsgType = {}));
class SetZoomMsg {
    constructor(zoomPct) {
        this.zoomPct = zoomPct;
        this.type = MsgType.SetZoomMsg;
    }
}
class SetOffsetMsg {
    constructor(offsetPx) {
        this.offsetPx = offsetPx;
        this.type = MsgType.SetOffsetMsg;
    }
}
class SetRotationMsg {
    constructor(rotationRad) {
        this.rotationRad = rotationRad;
        this.type = MsgType.SetRotationMsg;
    }
}
export function init() {
    return {
        zoomPct: 1,
        offsetPx: new Vec2(0, 0),
        rotationRad: 0,
    };
}
export function update(state, msg) {
    switch (msg.type) {
        case MsgType.SetZoomMsg:
            return { ...state, zoomPct: msg.zoomPct };
        case MsgType.SetOffsetMsg:
            return { ...state, offsetPx: msg.offsetPx };
        case MsgType.SetRotationMsg:
            return { ...state, rotationRad: msg.rotationRad };
        default:
            const never = msg;
            throw { "unexpected msg": msg };
    }
}
export function createSender(sendMessage) {
    return {
        setRotation: pct => sendMessage(new SetRotationMsg(pct)),
        setOffset: xyPx => sendMessage(new SetOffsetMsg(xyPx)),
        setZoom: pct => sendMessage(new SetZoomMsg(pct)),
    };
}
export function zoomToolUpdate(camera, dragState, input) {
    const xd = input.x - dragState.prevPoint.x;
    const zoomPct = camera.zoomPct + xd / 150;
    return new SetZoomMsg(zoomPct);
}
export function rotateToolUpdate(_camera, dragState, input) {
    const rotationRad = Math.atan2(input.y - dragState.clickPoint.y, input.x - dragState.clickPoint.x);
    return new SetRotationMsg(rotationRad);
}
export function moveToolUpdate(camera, dragState, input) {
    const xd = input.x - dragState.prevPoint.x;
    const yd = input.y - dragState.prevPoint.y;
    const offset = new Vec2(camera.offsetPx.x + xd, camera.offsetPx.y + yd);
    return new SetOffsetMsg(offset);
}
