import { Events, useEffect, component, invalidate, _, onChange, box, Ref, findDOMNode, } from "ivi";
import { div, span, ul, textarea, li, CONTENT } from "ivi-html";
import * as styles from "./scripting.scss";
export const Scripting = component(c => {
    const textareaRef = box(null);
    let code = "";
    function onInput(ev) {
        code = ev.target.value;
        invalidate(c);
    }
    function onKeyboard(ev) {
        const textareaNode = findDOMNode(textareaRef);
        if (textareaNode === null || textareaNode !== ev.target) {
            return;
        }
        if (ev.key === "Enter" && !ev.shiftKey && !ev.altKey) {
            ev.preventDefault();
            try {
                /* eslint-disable-next-line no-eval */
                console.log(eval("var gl = props.gl.value;\n" + code));
            }
            catch { }
            code = "";
            invalidate(c);
        }
    }
    const listenToKeyboard = useEffect(c, () => {
        document.body.addEventListener("keydown", onKeyboard);
        return () => {
            document.body.removeEventListener("keydown", onKeyboard);
        };
    });
    return (props) => {
        listenToKeyboard();
        return div(_, _, [
            div(_, _, [
                "Variables",
                ul(_, _, li(_, _, [span(styles.monospaced, _, "gl"), ": WebGLRenderingContext"])),
            ]),
            Ref(textareaRef, Events(onChange(onInput), textarea(styles.codeInput, {
                content: CONTENT(code),
            }))),
        ]);
    };
});
