export class Vec2 {
    constructor(x, y) {
        this.x = x;
        this.y = y;
    }
    eq(other) {
        return Vec2.eq(this, other);
    }
}
(function (Vec2) {
    function eq(l, r) {
        return l.x === r.x && l.y === r.y;
    }
    Vec2.eq = eq;
    function lerp(pct, begin, end) {
        return new Vec2(begin.x + (end.x - begin.x) * pct, begin.y + (end.y - begin.y) * pct);
    }
    Vec2.lerp = lerp;
    function distance(l, r) {
        const x = r.x - l.x;
        const y = r.y - l.y;
        return Math.sqrt(x * x + y * y);
    }
    Vec2.distance = distance;
})(Vec2 || (Vec2 = {}));
