export function create(initialValue) {
    return new SignalWithPush(initialValue);
}
export function map(signal, transform) {
    return new SignalTransform(signal, transform);
}
export function filter(signal, defaultValue, filter) {
    return new SignalFilter(signal, defaultValue, filter);
}
class SignalWithPush {
    constructor(initialValue) {
        this.handlers = [];
        this.currentValue = initialValue;
        this.signal = new SignalWrapper(this);
    }
    push(value) {
        this.currentValue = value;
        const handlers = this.handlers;
        for (let i = 0; i < handlers.length; i++) {
            handlers[i](value);
        }
    }
    value() {
        return this.currentValue;
    }
    subscribe(handler) {
        this.handlers.push(handler);
        return {
            dispose: () => {
                removeHandler(this.handlers, handler);
            },
        };
    }
}
function removeHandler(handlers, handler) {
    let idx = -1;
    for (let i = 0; i < handlers.length; i++) {
        if (handlers[i] === handler) {
            idx = i;
            break;
        }
    }
    if (idx !== -1) {
        handlers.splice(idx, 1);
    }
}
class SignalWrapper {
    constructor(baseSignal) {
        this.baseSignal = baseSignal;
    }
    value() {
        return this.baseSignal.value();
    }
    subscribe(handler) {
        return this.baseSignal.subscribe(handler);
    }
}
class SignalTransform {
    constructor(baseSignal, transform) {
        this.baseSignal = baseSignal;
        this.transform = transform;
    }
    value() {
        return this.transform(this.baseSignal.value());
    }
    subscribe(handler) {
        const transformedHandler = (val) => {
            handler(this.transform(val));
        };
        return this.baseSignal.subscribe(transformedHandler);
    }
}
class SignalFilter {
    constructor(baseSignal, defaultValue, filter) {
        this.baseSignal = baseSignal;
        this.filter = filter;
        const baseValue = baseSignal.value();
        this.currentValue = filter(baseValue) ? baseValue : defaultValue;
    }
    value() {
        return this.currentValue;
    }
    subscribe(handler) {
        const filter = this.filter;
        const filteredHandler = (val) => {
            if (filter(val)) {
                this.currentValue = val;
                handler(val);
            }
        };
        return this.baseSignal.subscribe(filteredHandler);
    }
}
